  font-family: "TT Hoves", sans-serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.3; }
  @media (min-width: 1300px) {
    body {
      font-size: 1.125rem; } }

input, button, textarea {
  font-family: inherit; }

.h2 {
  font-size: 1.375rem;
  line-height: 1.24;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: .02em; }
  @media (min-width: 1080px) {
    .h2 {
      font-size: 2rem; } }
  @media (min-width: 1300px) {
    .h2 {
      font-size: 2.75rem; } }

/* Для вебкит-браузеров (Chrome, Edge, Safari) */
::-webkit-scrollbar {
  width: 8px;
  height: 8px; }

::-webkit-scrollbar-track {
  background: transparent;
  border-radius: 10px; }

::-webkit-scrollbar-thumb {
  background: var(--color-primary);
  border-radius: 10px; }

::-webkit-scrollbar-thumb:hover {
  background: var(--color-primary-dark); }

/* Для Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: var(--color-primary) transparent; }

/* Для старых версий Firefox (если нужно) */
* {
  scrollbar-face-color: var(--color-primary);
  scrollbar-track-color: transparent;
  scrollbar-width: thin; }

*,
*:before,
*:after {
  box-sizing: border-box; }

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  overflow-x: hidden;
  color: var(--color-text);
  -webkit-font-smoothing: antialiased;
  background-color: var(--color-bg); }

.main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center; }

button {
  cursor: pointer; }

a {
  color: inherit;
  text-decoration: none; }

.container {
  width: 100%;
  max-width: 1264px;
  padding-left: 32px;
  padding-right: 32px;
  margin-left: auto;
  margin-right: auto; }

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  border: 1px solid;
  padding: 13px 24px;
  border-radius: 12px;
  font-size: 16px;
  line-height: 20px;
  font-weight: 500;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  transition: border-color 0.2s ease, background-color 0.2s ease, color 0.2s ease, box-shadow 0.2s ease; }
  .button--red {
    border-color: transparent;
    background-color: var(--color-primary);
    color: #fff; }
    .button--red:hover {
      background-color: var(--color-primary-dark); }
  .button--round-corner {
    border-radius: 40px; }
  .button--outline-red {
    border: 1px solid var(--color-primary);
    background-color: var(--color-bg);
    color: var(--color-text); }
    .button--outline-red:hover {
      background-color: var(--color-primary);
      color: var(--color-bg); }
  .button--outline-gray {
    border: 1px solid #EAEAEF;
    background-color: var(--color-bg);
    color: var(--color-text); }
    .button--outline-gray:hover {
      background-color: var(--color-primary);
      border-color: var(--color-primary);
      color: var(--color-bg); }
  .button--outline-white {
    border-color: rgba(255, 255, 255, 0.5);
    color: #fff; }
    .button--outline-white:hover {
      border-color: #fff;
      background-color: #fff;
      color: var(--color-primary); }
  .button--icon-only {
    padding: 13px;
    aspect-ratio: 1; }
  .button--with-icon {
    padding-left: 16px;
    padding-right: 16px; }
  .button--xl {
    padding: 18px 40px;
    font-weight: 600; }
  .button:disabled {
    filter: grayscale(1);
    cursor: not-allowed; }
  .button__icon {
    display: block;
    width: 20px;
    height: 20px; }
    .button__icon--color-red {
      color: var(--color-primary); }
      .button:hover .button__icon--color-red {
        color: inherit; }

.input {
  display: block;
  width: 100%;
  min-width: unset;
  padding: 16px;
  border: 1px solid;
  border-radius: 12px;
  background-color: transparent;
  font-size: 16px;
  line-height: 24px;
  color: var(--color-text);
  transition: border-color 0.2s ease; }
  .input::placeholder {
    color: #9DA2A8; }
  .input:hover {
    border-color: var(--color-primary-light); }
  .input:focus {
    border-color: var(--color-primary);
    outline: none; }

.theme-light .input {
  border-color: #EDEEF1; }

.theme-dark .input {
  border-color: #9DA2A8; }

.form__input {
  display: block;
  width: 100%;
  margin-bottom: 1.5rem; }

.form__submit {
  width: 100%;
  margin-bottom: 1rem; }

.checkbox {
  position: relative;
  display: block; }
  .checkbox__input {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0; }
  .checkbox__text {
    display: inline-block;
    padding-left: 24px;
    font-size: 14px;
    line-height: 16px;
    user-select: none;
    color: var(--color-text); }
    .checkbox__text::before {
      position: absolute;
      top: 0;
      left: 0;
      width: 16px;
      height: 16px;
      border: 1px solid #EDEEF1;
      border-radius: 4px;
      background-position: center;
      background-size: 100% 100%;
      background-repeat: no-repeat;
      cursor: pointer;
      content: "";
      transition: border-color 0.2s ease, background-color 0.2s ease; }
    .checkbox__text:hover::before {
      border-color: var(--color-primary); }
    .checkbox__input:checked + .checkbox__text::before {
      border-color: transparent !important;
      background-color: var(--color-primary);
      background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 16 16' xmlns='http://www.w3.org/2000/svg' fill='%23ffffff' class='bi bi-check'%3E%3Cpath d='M10.97 4.97a.75.75 0 0 1 1.07 1.05l-3.99 4.99a.75.75 0 0 1-1.08.02L4.324 8.384a.75.75 0 1 1 1.06-1.06l2.094 2.093 3.473-4.425a.267.267 0 0 1 .02-.022z'/%3E%3C/svg%3E"); }
  .checkbox__link {
    color: currentColor;
    text-decoration: underline;
    transition: color 0.2s ease; }
    .checkbox__link:hover {
      text-decoration: none;
      color: var(--color-primary); }

.input {
  display: block;
  width: 100%;
  min-width: unset;
  padding: 16px;
  border: 1px solid;
  border-radius: 12px;
  background-color: transparent;
  font-size: 16px;
  line-height: 24px;
  color: var(--color-text);
  transition: border-color 0.2s ease; }
  .input::placeholder {
    color: #9DA2A8; }
  .input:hover {
    border-color: var(--color-primary-light); }
  .input:focus {
    border-color: var(--color-primary);
    outline: none; }

.theme-light .input {
  border-color: #EDEEF1; }

.theme-dark .input {
  border-color: #9DA2A8; }

.form-group__label {
  display: block;
  margin-bottom: 8px;
  font-size: 14px;
  line-height: 21px;
  font-weight: 600; }

.form-group__input-box {
  position: relative; }

.form-group__btn {
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  border-radius: 12px;
  aspect-ratio: 1;
  border: none;
  background-color: transparent;
  color: #9DA2A8;
  transition: background-color 0.2s ease, color 0.2s ease; }
  .form-group__btn:hover {
    background-color: var(--color-primary);
    color: #fff; }

.form-group__btn-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: block;
  width: 24px;
  height: 24px;
  transition: opacity 0.2s ease; }
  .form-group__btn--active .form-group__btn-icon--normal {
    opacity: 0; }
  .form-group__btn-icon--active {
    opacity: 0; }
    .form-group__btn--active .form-group__btn-icon--active {
      opacity: 1; }

.theme-switcher {
  position: relative;
  z-index: 0;
  display: inline-flex;
  padding: 1px;
  border: 1px solid #EAEAEF;
  border-radius: 50px; }
  .theme-switcher::before {
    position: absolute;
    z-index: -1;
    top: 1px;
    left: 1px;
    display: block;
    width: 24px;
    height: 24px;
    background-color: var(--color-primary);
    border-radius: 50px;
    content: "";
    transition: transform 0.2s ease; }
  .theme-switcher__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 24px;
    height: 24px;
